<template>
	<view class="box">
		<view class="phone">
			<view class="text">
				填写收菜地址
			</view>
			<view class="text-right">
				<text>04</text>
				<text>/</text>
				<text>04</text>
			</view>
		</view>
		<view class="">
			<input class="inputs" type="text" v-model="array.Name" placeholder="请输入收菜人姓名">
			<city-picker class="picker" @change="onPickerChange" :range="range" :value="value">
				<view class="inputs address">
					<input class="" disabled="true" type="text" v-model="value" placeholder="所在地区">
					<image src="@/static/img/icon_go@2x.png" mode=""></image>
				</view>
				<!-- <text :class="[{ placeholder: !value }]">{{value || '请选择'}}</text> -->
			</city-picker>
			<input class="inputs" type="text" v-model="array.Address" placeholder="详细地址">
		</view>
		<view class="btn" @click="onFinish">完成</view>
	</view>
</template>

<script>
	import CityPicker from '@/components/city-picker/index.vue'

	export default {
		components: {
			CityPicker
		},
		data() {
			return {
				range: [],
				value: "",
				array: {
					Name: '',
					Province: '',
					City: '',
					Region: '',
					Address: ''
				}
			}
		},
		methods: {
			/**
			 * 获取省市区列表
			 * @param {Object} list
			 */
			onPickerChange(list) {
				// console.log(list)
				this.range = list.map(item => {
					// console.log(item)
					item = [{
						label: item.label,
						value: item.value,
					}]
					return item
				})
				this.value = `${list[0].label||""}${list[1].label||''}${list[2].label||''}`
				this.array.Province = list[0].label
				// this.array.Province = list[0].value
				this.array.City = list[1].label
				// this.array.City = list[1].value
				this.array.Region = list[2].label
				// this.array.Region = list[2].value

			},
			/**
			 * 判断用户否已填写信息
			 */
			validateForm() {
				if (!this.validForm(this.array, [{
							key: "Name",
							text: "请填写收菜人姓名"
						},
						{
							key: "Address",
							text: "请填写详细地址"
						}
					])) {
					return false
				}
				return true
			},
			onFinish() {
				if (!this.validateForm()) {
					return false
				}
				if (!this.value) {
					uni.showToast({
						title: '请选择省市区',
						icon: 'none'
					});
					return false
				}
				this.$emit("onFinish", this.array)
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		padding: 40rpx 30rpx 0;
	}

	.phone {
		font-size: 40rpx;
		font-weight: 500;
		color: #333333;
		line-height: 56rpx;
		display: flex;
		justify-content: space-between;

		.text-right {
			text:nth-of-type(1) {
				width: 42rpx;
				height: 56rpx;
				font-size: 40rpx;
				font-weight: 500;
				color: #00D08F;
				line-height: 56rpx;
			}

			text:nth-of-type(2) {
				width: 20rpx;
				height: 56rpx;
				font-size: 40rpx;
				font-weight: 500;
				color: #999990;
				line-height: 56rpx;
			}

			text:nth-of-type(3) {
				width: 48rpx;
				height: 56rpx;
				font-size: 40rpx;
				font-weight: 500;
				color: #333333;
				line-height: 56rpx;
			}

		}
	}

	.inputs {
		width: 100%;
		border-bottom: 2rpx solid #efefef;
		font-size: 32rpx;
		font-weight: 400;
		line-height: 44rpx;
		padding: 40rpx 0 30rpx;
	}

	.address {
		display: flex;
		justify-content: space-between;

		image {
			width: 36rpx;
			height: 36rpx;
		}
	}

	.btn {
		margin-top: 60rpx;
		width: 690rpx;
		height: 104rpx;
		background: #00D08F;
		border-radius: 52rpx;
		backdrop-filter: blur(20rpx);
		font-size: 32rpx;
		font-weight: 500;
		color: #FFFFFF;
		line-height: 104rpx;
		text-align: center;
	}
</style>
